@import url(https://fonts.googleapis.com/css?family=Chivo);
@import url(https://fonts.googleapis.com/css?family=Tajawal);
.list-align {
    padding: 1rem 8rem 0 8rem;
    min-height: calc(100vh - 13rem - 5.1rem - 1.2rem);
}


.list-results {
    color: #888;
    font-weight: bold;
    font-size: 1.1rem;
    padding: 1rem 8rem 0 8rem;
}

.list-container {
    display: grid;
    grid-template-columns: repeat(3, 28%);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.product-shower {
    cursor: pointer;
}

.plus,
.list-add-cart {
    height: 2rem;
    width: 2rem;
    font-size: 1.5rem;
    color: #fff;
    position: relative;
    left: 85%;
    transition-timing-function: cubic-bezier(1, 0, 0.28, 1);
    transition-duration: 0.2s;
    z-index: 1;
}

.product-shower:hover .plus {
    left: 90%;
    color: #bbb;
}

.product-shower:hover .plus:hover {
    color: #000;
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
}


.list-add-cart {
    height: 3rem;
    width: 6rem;
    left: 0;
    top: 2%;
    font-size: 1.1rem;
}

.product-shower:hover .list-add-cart {
    left: 1rem;
    color: #000000;
}

.list-img-container {
    text-align: center;
    vertical-align: middle;
    position: relative;
}

.list-img1 {
    width: 80%;
    height: auto;
    margin: auto;
    position: relative;
    transition: opacity 0.2s ease-in-out;
}

.list-img2 {
    position: absolute;
    z-index: 0;
    width: 80%;
    height: auto;
    left: 10%;
    bottom: 0;
}

.list-img1 {
    z-index: 1;
}

.product-shower:hover .list-img1 {
    opacity: 0;
}

.list-review {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.list-name {
    width: 10rem;
    color: #999;
    font-weight: bold;
    font-size: 1.1rem;
    margin-left: 1rem;
}

.list-price {
    margin-right: 1rem;
}
.mycontainer,
.mycontainer2 {
    margin: 2rem;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 90vh;
}

.mycontainer2 {
    margin: 0 10rem;
    grid-template-columns: 50% 50%;
    height: 100%;
    grid-gap: 2rem;
}

.title,
.text,
.price,
.imgposition,
.starposition {
    text-align: center;
}

.starposition {
    display: -webkit-flex;
    display: flex;
    margin: 0 5rem;
    -webkit-justify-content: center;
            justify-content: center;
}

.title {
    font-size: 1.2rem;
}

.text {
    font-size: 1rem;
    letter-spacing: 0.5px;
    font-weight: 500;
    line-height: 1.7;
    color: #888;
}

.price {
    font-weight: bold;
}

.img,
.img_size {
    max-width: 80%;
}

.img_size2 {
    margin-bottom: 6rem;
    width: 75%;
}

.img-content {
    text-align: center;
    -webkit-align-self: center;
            align-self: center;
}

.img-content2,
.review-content2 {
    grid-column: span 2;
    text-align: center;
    align-self: center;
    width: 100%;
}

.cart-form {
    display: -webkit-flex;
    display: flex;
}

.cartbutton {
    display: inherit;
    border: 1px solid #aaa;
    border-right: 0px;
    border-radius: 100px 0 0 100px;
    height: 3rem;
    margin-top: 1.5rem;
    width: 35%;
}

.count {
    width: 80%;
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    font-weight: normal;
}

.cartbutton-up,
.cartbutton-down {
    border-left: 1px solid #aaa;
    height: 50%;
    width: 100%;
    cursor: pointer;
}

i.icon {
    margin: 0 4px;
}

.cartbutton-down {
    border-top: 1px solid #aaa;
}

.addcartbutton {
    border: 1px solid #aaa;
    border-left: 0px;
    border-radius: 0 100px 100px 0;
    height: 3rem;
    margin-top: 1.5rem;
    width: 65%;
    background-color: black;
    color: white;
    cursor: pointer;
}

.addcartbutton p {
    margin-top: 0.7rem;
}

.review-title {
    letter-spacing: 1.5px;
    line-height: 2rem;
    font-size: 2.0rem;
    margin: 0 0 2rem 0;
    font-weight: bold;
    font-family: 'Tajawal', sans-serif;
}

.review-slogan {
    line-height: 2rem;
    font-size: 1.5rem;
    margin: 1rem 0 1rem 0;
    font-weight: 100;
    font-family: 'Tajawal', sans-serif;
}

.review-comment {
    line-height: 2rem;
    font-size: 1.15rem;
    margin: 0 0 1rem 0;
    color: #888;
}

.productDetailLoading {
    position: relative;
    min-height: 75vh;
}

.productDetailLoadingText {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 30px;
}
.cd-fixed-bg {
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center center;
    display: grid;
    grid-template-columns: 70%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 110vh;
}

.img-size {
    margin: auto;
    cursor: pointer;
    width: auto;
    height: auto;
    max-width: 80%;
    max-height: 100%;
}
.logo {
    width: 103px;
    height: 22px;
}

.blockSize {
    margin: auto;
    margin-top: 17px;
    text-align: center;
}
.footer-main-container {
    position: relative;
    background: #555;
}

.footer-container {
    display: -webkit-flex;
    display: flex;
    height: 13rem;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: #555;
    color: rgb(255, 255, 255);
}

.footer-item-container {
    max-width: 10rem;
    margin: 0.5rem;
}

.footer-item {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    height: auto;
    margin: auto;
}

.footer-name-container {
    background: #555;
    color: rgb(255, 255, 255);
    text-align: center;
    position: relative;
    bottom: 1.2rem;
}
html,
body {
    width: 100vw;
}
